import { isEmpty } from "lodash";

const namespaced = true;

const state = {
  messages: [],
  attachments: [],

  message: "",
  message_attachments: [],

  reply_to: null,
  reply_to_shadow: null
};

const getters = {};

const mutations = {
  CLEAR_INPUT: state => (state.message = ""),

  PUSH_MESSAGE: (state, message) => state.messages.push(message),

  SET_MESSAGES: (state, messages) => (state.messages = messages),

  INSERT_ATTACHMENT: (state, data) => {
    state.messages = state.messages.map(m => {
      if (m.hash == data.hash) {
        let attachments = [...m.attachments, data.attachment];

        return {
          ...m,
          attachments
        };
      }

      return m;
    });
  },

  UPDATE_MESSAGE: (state, data) => {
    state.messages = state.messages.map(m => {
      if (m.hash == data.hash) {
        return {
          ...m,
          ...data
        };
      }

      return m;
    });
  }
};

const actions = {
  async fetchMessages({ commit }, session) {
    let { data } = await axios.get(`/plugin/session/${session}/messages`);

    commit("SET_MESSAGES", data.messages);
  },

  async sendToBot({ commit, rootState, state }) {
    commit("SET_LOADING", true);

    let { data } = await axios.get(`/chatbot/1/message`, {
      params: {
        client_id: rootState.client.client.id,
        message_from: "client",
        message: state.message
      }
    });

    data.forEach(messageRow => {
      commit("PUSH_MESSAGE", {
        message_from: "admin",
        message: messageRow.random()
      });
    });

    commit("SET_LOADING", false);
  },

  async sendToLive({ commit, rootState, state }) {
    commit("SET_LOADING", true);

    let data = {
      client_id: rootState.client.client.id,
      message_from: "client",
      message: state.message,
      session: localStorage.getItem("LCS_Session")
    };

    if (!isEmpty(state.attachments)) data.attachments = state.attachments;

    await axios.post(`/message`, data);

    commit("SET_LOADING", false);
  }
};

export default {
  namespaced,
  state,
  mutations,
  getters,
  actions
};
