const IS_DEVELOPMENT = process.env.NODE_ENV === "development" ? true : false;
const BASE_URL = IS_DEVELOPMENT ? process.env.MIX_LOCAL_URL : process.env.MIX_PRODUCTION_URL;
const PUSHER_APP_CLUSTER = IS_DEVELOPMENT ? process.env.MIX_LOCAL_PUSHER_APP_CLUSTER : process.env.MIX_PROD_PUSHER_APP_CLUSTER;
const PUSHER_APP_KEY = IS_DEVELOPMENT ? process.env.MIX_LOCAL_PUSHER_APP_KEY : process.env.MIX_PROD_PUSHER_APP_KEY;

export {
  IS_DEVELOPMENT,
  BASE_URL,
  PUSHER_APP_KEY,
  PUSHER_APP_CLUSTER,
};
