const namespaced = true;

const state = {
  client: {},
  client_is_loading: false
};

const getters = {};

const mutations = {
  SET_LOADING: (state, status) => (state.client_is_loading = status),

  SET_CLIENT: (state, client) => (state.client = client)
};

const actions = {
  async verifyClient({ commit }) {
    commit("SET_LOADING", true);

    let token = localStorage.getItem("LCS_Token");
    let { data } = await axios.get(`/client/${token}/verify`, {
      params: {
        cookie: localStorage.getItem("LCS_Encrypted")
      }
    });
    commit("SET_CLIENT", data.data);
    commit("session/SET_SESSIONS", data.data.sessions, {
      root: true
    });

    // store/update the token for verified users
    localStorage.setItem("LCS_Token", data.data.token);

    commit("SET_LOADING", false);
  },

  async createClient({ commit }) {
    commit("SET_LOADING", true);

    let { data } = await axios.post(`/client`, {
      cookie: localStorage.getItem("LCS_Encrypted")
    });
    commit("SET_CLIENT", data.data);
    commit("session/SET_SESSIONS", data.data.sessions, {
      root: true
    });

    // store the token for newly created users
    localStorage.setItem("LCS_Token", data.data.token);

    commit("SET_LOADING", false);
  }
};

export default {
  namespaced,
  state,
  mutations,
  getters,
  actions
};
