<template>
  <div class="header">
    <div class="header-back" @click="backToList()">
      <InlineSvg name="heroicons/chevron-left.svg" color="#fff" size="20px" />
    </div>

    <div class="header-meta">
      <div class="header-logo">
        <img v-if="$isEmpty(agent)" :src="`${$IMAGE_URL}/icon.png`" alt="Widget Icon" />
        <img v-else :src="`${$BASE_URL}/storage/uploads/users/${agent.id}/${agent.profile_picture}`" :alt="agent.bio.first_name" />
        <!-- <div v-else class="header-logo-initial">{{ agent.bio.first_name[0] }}</div> -->
      </div>

      <div class="header-title">
        <h5 class="header-title">{{ widget.ui_header_title }}</h5>
        <small class="header-title-sub" v-html="headerSubtitle()"></small>
      </div>
    </div>

    <!-- <div class="header-close" @click="isWidgetOpen = !isWidgetOpen">
      <InlineSvg name="heroicons/close.svg" color="#fbc58f" size="20px" />
    </div> -->
  </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "ChatHeader",
  computed: {
    ...mapState("client", ["client"]),
    ...mapState("widget", ["agent", "widget"]),
    ...mapState("session", ["session"]),

    isWidgetOpen: {
      get() {
        return this.widget.is_open;
      },
      set(newValue) {
        this.widget.is_open = newValue;
      }
    }
  },
  methods: {
    async backToList() {
      // fetch all the sessions with messages
      // let { data } = await axios.get(`/client/${this.client.token}/sessions`);
      // this.$store.commit("session/SET_SESSIONS", data.sessions);

      // update the session from the sessions
      this.$store.commit("session/UPDATE_SESSION", this.session);

      // remove the session from store
      this.$store.state.session.session = {};

      // remove the session from storage
      localStorage.removeItem("LCS_Session");

      // go back to list
      this.widget.operation = "list";
    },
    headerSubtitle() {
      let t = new Date();
      let x = new Date(`${t.getMonth() + 1}/${t.getDate()}/${t.getFullYear()} 08:00:00`).getTime();
      let y = new Date(`${t.getMonth() + 1}/${t.getDate()}/${t.getFullYear()} 17:00:00`).getTime();

      if (t.getTime() >= x && t.getTime() <= y) {
        return "<div class='header-title-online-indicator'></div> Need help? We're online!";
      } else {
        return "<div class='header-title-offline-indicator'></div> We'll be back tomorrow.";
      }
    }
  }
};
</script>
