<template>
  <div id="filichat">
    <div class="wrapper" :class="{ open: isWidgetOpen }">
      <template v-if="['list'].includes(widget.operation)">
        <ChatWelcome />
      </template>

      <template v-if="['create', 'show'].includes(widget.operation)">
        <ChatHeader />
        <ChatBody />
        <ChatFooter />
      </template>
    </div>
    <div class="toggler" :class="{ open: isWidgetOpen }" @click="isWidgetOpen = !isWidgetOpen">
      <img :class="{ 'toggler-open': !isWidgetOpen, 'toggler-close': isWidgetOpen }" :src="`${$IMAGE_URL}/${!isWidgetOpen ? toggler.open : toggler.close}`" alt="Toggler" />
    </div>
  </div>
</template>

<script>
import { PUSHER_APP_CLUSTER, PUSHER_APP_KEY } from "@Scripts/constants";
import { mapState } from "vuex";
import CssVars from "css-vars-ponyfill";

export default {
  name: "Widget",
  data() {
    return {
      toggler: {
        open: "widget/toggler-open.svg",
        close: "widget/toggler-close.svg"
      }
    };
  },
  computed: {
    ...mapState("client", ["client"]),
    ...mapState("session", ["session", "sessions"]),
    ...mapState("widget", ["widget"]),

    message: {
      get() {
        return this.$store.state.message.message;
      },
      set(newValue) {
        this.$store.state.message.message = newValue;
      }
    },
    isWidgetOpen: {
      get() {
        return this.$store.state.widget.is_open;
      },
      set(newValue) {
        this.$store.state.widget.is_open = newValue;
      }
    }
  },
  methods: {
    setupCssVars() {
      CssVars({
        variables: {
          ui_header_color: this.widget.ui_header_color,
          ui_client_bg: this.widget.ui_client_bg,
          ui_client_color: this.widget.ui_client_color,
          ui_robot_bg: this.widget.ui_robot_bg,
          ui_robot_color: this.widget.ui_robot_color,
          ui_color_primary: this.widget.ui_color_primary,
          ui_color_secondary: this.widget.ui_color_secondary
        }
      });
    },
    setupListeners() {
      const self = this;

      // set up pusher's listeners
      const PUSHER = new Pusher(PUSHER_APP_KEY, {
        cluster: PUSHER_APP_CLUSTER,
        encrypted: true
      });

      // channel to subscribe
      const CH_SESSION = PUSHER.subscribe("session");
      const CH_MESSAGE = PUSHER.subscribe("message");

      // listen to session channels
      CH_SESSION.bind(`session.ended`, data => {
        let { session } = data;

        // attach the message
        self.$store.commit("message/PUSH_MESSAGE", {
          message_from: "session",
          message: `
            Your session has been marked as resolved. <br />
            Thank you for chatting!
          `
        });

        localStorage.removeItem("LCS_Session");
      });

      CH_SESSION.bind(`session.update`, data => {
        let { session } = data;

        self.$store.commit("session/UPDATE_SESSION", session);
      });

      CH_MESSAGE.bind(`message.from.admin`, data => {
        let { message, session, token } = data;

        // make sure the message belongs to the client
        if (self.client.token == token) {
          // push the message if you are viewing the session
          if ("session" in self.session && self.session.session == session) {
            // mart it as read
            axios.put(`/plugin/session/${session}/seen`);

            self.$store.commit("message/PUSH_MESSAGE", { ...message, attachments: [] });
          } else {
            // if you do not select any session at the moment
            self.$store.commit("session/PUSH_MESSAGE", { session, message });
          }
        }
      });

      CH_MESSAGE.bind(`attachment.created`, async data => {
        let { attachment, client, message, session } = data;

        self.$store.commit("message/INSERT_ATTACHMENT", {
          ...message,
          attachment
        });
      });
    }
  },
  async created() {
    // setup css variable
    this.setupCssVars();

    // setup pusher listeners
    this.setupListeners();

    // remove any existing session
    localStorage.removeItem("LCS_Token");
    localStorage.removeItem("LCS_Session");

    console.log(`Counter's Start.`);

    let seconds = 0;
    let counter = setInterval(() => {
      seconds = seconds + 1;
      console.log(`${seconds} seconds.`);
    }, 1000);

    if (localStorage.getItem("LCS_Token")) {
      // verify client if token exist
      await this.$store.dispatch("client/verifyClient");
    } else {
      // create client if token not exist
      await this.$store.dispatch("client/createClient");
    }

    clearInterval(counter);
    console.log(`Counter's Done.`);
  }
};
</script>
